<template>
	<div class="body fundManage_body">
		<div class="table_totle_box flex_between">
			<div class="table_totle_box_left">
				在管基金总数<span>(&nbsp;{{ totalCount }}&nbsp;)</span>
			</div>
			<div class="table_totle_box_right">
				<div class="table-group-title-btn btn-blue" @click="downloadExcel">
					<span class="iconfont icon-daochu"></span>导出excel表格
				</div>
			</div>
		</div>
		<div class="main under_fund">
			<div class="under_fund_main table">
				<el-table ref="multipleTable" :data="tableData" border style="width: 100%" max-height="350">
					<el-table-column prop="" label="序号" width="100">
						<template slot-scope="scope">
							{{ (scope.$index + 1) + (currentPage - 1) * pageSize }}
						</template>
					</el-table-column>
					<el-table-column prop="short_name" label="基金简称" width="150">
					</el-table-column>
					<el-table-column prop="name" label="合伙企业" width="200">
					</el-table-column>
					<el-table-column prop="fund_type_name" label="在管基金类别" width="180">
					</el-table-column>
					<el-table-column prop="unit" label="币种" width="180">
						<template slot-scope="scope">
							{{ scope.row.unit == 1?'人民币':'美元' }}
						</template>
					</el-table-column>
					<el-table-column prop="subscription_amount" label="认缴规模（万元）" width="180">
					</el-table-column>
					<el-table-column prop="amount_paid_in" label="实缴规模（万元）" width="180">
					</el-table-column>
					<el-table-column prop="back" label="回款返本（万元）" width="140">
					</el-table-column>
					<el-table-column prop="income" label="回款收益-税后（万元）" width="180">
					</el-table-column>
					<el-table-column prop="irr" label="XIRR" width="150">
					</el-table-column>
					
					<el-table-column prop="multiple" label="倍数" width="150">
					</el-table-column>
					<el-table-column prop="fair_value" label="最新公允（万元）" width="150">
					</el-table-column>
					
					<el-table-column prop="protocol_end_date" label="基金到期日" width="150">
					</el-table-column>
					<el-table-column prop="fund_state_name" label="备案状态" width="120">
					</el-table-column>
					<el-table-column fixed="right" label="操作" width="150">
						<template slot-scope="scope">
							<el-button type="text" size="small" @click="view(scope.row)">查看</el-button>
						</template>
					</el-table-column>
					<template slot="empty">
						<div class="table-nodata flex_start">
							<img src="../../images/table_nodata.png">
							<span>没有对应数据</span> 
						</div>
					</template>
				</el-table>
				<el-pagination v-if="totalCount" @size-change="everyPageSizeChange" prev-text="上一页" next-text="下一页" background @current-change="changeCurrentPage" :current-page="currentPage" :page-size="pageSize" layout="sizes, prev, pager, next, jumper" :total="totalCount">
    			</el-pagination>
			</div>
		</div>
	</div>
</template>

<script>
	import { dialogConfirm, dialogConfirmHasbtn } from '../../config/mUtils'
	import { getCompanyRunUnderFunListApi,downloadExcelCompanyRunUnderFundApi } from '../../config/api'

	export default {
		name: 'subFundRunInvestGroup',
		data() {
			return {
				id: '',
				currentPage: 1,
				pageSize: 10,
				totalCount: 0,
				tableData: [],
			}
		},
		created() {
			this.id = this.$route.query.id;
			this.getCompanyRunUnderFunList()
		},
		mounted() {},
		components: {},
		methods: {
			getCompanyRunUnderFunList(){
				getCompanyRunUnderFunListApi({
					id: this.id,
					page: this.currentPage,
					size: this.pageSize
				}).then(res => {
					if(res.data.msgNo == 1) {
						this.tableData = res.data.payload.data;
						this.totalCount = res.data.payload.count;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			downloadExcel(){
				downloadExcelCompanyRunUnderFundApi({
					id: this.id
				}).then(res => {
					if(res.data.msgNo == 1) {
						window.location.href = res.data.payload.url;
					} else {
						dialogConfirm("info", res.data.message);
					}
				})
			},
			view(e) {
				var type = e.fund_type;
				var component = '';
				if (type == 1) {
					component = "motherFundCompanyDetail";
				} else{
					component = "selfAdministerFundCompanyDetail";
				}
				this.$router.push({
					name: component,
					query: {
						id: e.fund_id
					}
				})
			},
			// 分页功能模块
			everyPageSizeChange(e){
				this.pageSize = e;
				this.getCompanyRunUnderFunList()
			},
			changeCurrentPage(e){
				this.currentPage = e;
				this.getCompanyRunUnderFunList()
			},
		},
	}
</script>

<style scoped="scoped">
	.body {
		padding: 0 0px 30px 0px;
	}
	.table_totle_box_left{
		color: #6292F4;
	}
</style>


